import { Component, OnInit } from "@angular/core";
import { HttpClient } from "@angular/common/http";

@Component({
  selector: "app-win",
  templateUrl: "./win.component.html",
  styleUrls: ["./win.component.scss"]
})
export class WinComponent implements OnInit {
  co: number;
  time: boolean;
  nday: any;
  cyear: any;
  cdate: any;
  cday: any;
  myday: any;
  constructor(private http: HttpClient) {}

  ngOnInit() {
    this.myday = window.localStorage.getItem("myday");
    const timer = window.localStorage.getItem("time");
    if (timer === "true") {
      this.time = true;
      this.nday = window.localStorage.getItem("present");
      setInterval(function() {
        const d = new Date();
        const f = new Date(window.localStorage.getItem("present"));
        const diff = d.valueOf() - f.valueOf();

        document.getElementById("ss").innerHTML =
          Math.abs(Math.round(diff / 1000 / 60 / 60)) +
          "<small> hours </small>" +
          Math.abs(Math.round((diff / 1000 / 60) % 60)) +
          "<small> minutes left </small>";
      }, 1000);
    }

    let c = new Date(window.localStorage.getItem("present")).getSeconds();
  }
  setTime() {
    window.localStorage.setItem("time", "true");

    this.time = true;
    this.myday = new Date();
    window.localStorage.setItem("myday", this.nday);
    this.cday = this.myday.getDay();
    this.cdate = this.myday.getDate();
    this.cyear = this.myday.getYear();

    const d1 = new Date();
    const d2 = new Date();
    d1.setHours(+d2.getHours() + 9);
    d1.setMinutes(new Date().getMinutes() + 30);
    this.nday = d1;
    window.localStorage.setItem("present", this.nday);
  }
  clear() {
    window.localStorage.removeItem("time");
    window.localStorage.removeItem("present");
    window.localStorage.clear();
  }
}
